<template>
  <div class="hello">
      {{ user.nick_nm }}님이 로그인 됐습니다.<br>
     <button v-on:click="test">sdfa</button>

     <br>
     <button v-on:click="test2">test2</button>
  </div>
</template>

<script>
import final from '../final'

export default {
  name: 'Home',
  data () {
    return {
      user: this.$store.state.user
    }
  },
  beforeCreate() {
    this.$socket.emit('login', this.$store.state.user)
  },
  sockets: {
      connect: function() {
          console.log('sockets - login')
      },

      somebodyLogin: function(data) {
          console.log('somebodyLogin : ' + data.i_user)
      }
  },
  methods: {
    test() {
      this.$axios.get('/users/myinfo').then(function(result) {
        console.log(result.data);
      });
    },

    test2() {
      this.$axios.get('/auth/test').then(function(result) {
        console.log(result.data);
      });
    }
  }
}
</script>



